@mixin font-size-default($size: 14) {
    $lineHeight: $size + 4;
    font-size: $size + px;
    line-height: $lineHeight + px;
}

@mixin mobile-block() {
    @media screen and (max-width: 768px) {
        width: 100%;
    }
}

@mixin multi-line ($num) {
    word-break: break-all;
    display: -webkit-box;
    -webkit-line-clamp: $num;
    -webkit-box-orient: vertical;
    overflow: hidden;
}

@mixin single-line {
    overflow: hidden;
    text-overflow: ellipsis;
    -o-text-overflow: ellipsis;
    -webkit-text-overflow: ellipsis;
    -moz-text-overflow: ellipsis;
    white-space: nowrap;
}
.resizable-handle {
  // background-color: red;
  position: absolute;
  left: 0px;
  bottom: 0;
  z-index: 1;
  width: 10px;
  height: 100%;
  cursor: col-resize;
}

.box {
  flex: 1;
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  // padding-left: 10px;
  &-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-weight: 500;

    @include font-size-default(14);
    padding-left: 4px;
    padding-right: 4px;
    height: 36px;
    background-color: #060606;
    color: #fff;

    &-left {
        
    }

    &-right {
        padding-right: 4px;
    }
  }

  &-body {
    flex: 1;
    overflow: auto;
  }
}
