@mixin font-size-default($size: 14) {
    $lineHeight: $size + 4;
    font-size: $size + px;
    line-height: $lineHeight + px;
}

@mixin mobile-block() {
    @media screen and (max-width: 768px) {
        width: 100%;
    }
}

@mixin multi-line ($num) {
    word-break: break-all;
    display: -webkit-box;
    -webkit-line-clamp: $num;
    -webkit-box-orient: vertical;
    overflow: hidden;
}

@mixin single-line {
    overflow: hidden;
    text-overflow: ellipsis;
    -o-text-overflow: ellipsis;
    -webkit-text-overflow: ellipsis;
    -moz-text-overflow: ellipsis;
    white-space: nowrap;
}
.editor {
    padding: 12px;
    // height: 100%;
    // max-height: 100%;
    height: calc(100vh - 130px);
    overflow: hidden;
    display: flex;
    flex-direction: column;
    :global {
        .react-resizable {
            position: relative;
            background-clip: padding-box;
        }
    }
}

.code-div {
    background-color: #fff;
    display: flex;
    flex-direction: row;
    // height: 300px;
    // & > div {
    //     width: 33.33%;
    // }
}

.iframe-div {
    flex: 1;
    min-height: 100px;
    position: relative;
    background-color: #fff;
    display: flex;
    flex-direction: column;
}

.preview-iframe {
    // position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 10;
    border: 0;
}