@mixin font-size-default($size: 14) {
    $lineHeight: $size + 4;
    font-size: $size + px;
    line-height: $lineHeight + px;
}

@mixin mobile-block() {
    @media screen and (max-width: 768px) {
        width: 100%;
    }
}

@mixin multi-line ($num) {
    word-break: break-all;
    display: -webkit-box;
    -webkit-line-clamp: $num;
    -webkit-box-orient: vertical;
    overflow: hidden;
}

@mixin single-line {
    overflow: hidden;
    text-overflow: ellipsis;
    -o-text-overflow: ellipsis;
    -webkit-text-overflow: ellipsis;
    -moz-text-overflow: ellipsis;
    white-space: nowrap;
}
.vwood-resizer {
    flex: 1;
    display: flex;
    overflow: hidden;

    :global {
        .react-resizable {
            position: relative;
        }

        .react-resizable-handle {
            display: inline-block;
            width: 1px;
            height: 100%;
            background-repeat: no-repeat;
            background-origin: content-box;
            box-sizing: border-box;
            background-position: bottom right;
            // border-left: 1px solid #e8e8e8;
            cursor: col-resize;
            position: relative;
            background-color: rgb(221, 221, 221);
            padding: 0;

            &::after {
                content: "";
                display: inline-block;
                width: 12px;
                height: 100%;
                background-color: rgb(117, 117, 117);
                // border-radius: 2px;
                position: absolute;
                z-index: 10;
                inset: 0;
                // top: 50%;
                // left: 50%;
                transform: translateX(-50%)
            }
        }

        .react-resizable-handle-sw {
            bottom: 0;
            left: 0;
            cursor: sw-resize;
            transform: rotate(90deg);
        }

        .react-resizable-handle-se {
            bottom: 0;
            right: 0;
            cursor: se-resize;
        }

        .react-resizable-handle-nw {
            top: 0;
            left: 0;
            cursor: nw-resize;
            transform: rotate(180deg);
        }

        .react-resizable-handle-ne {
            top: 0;
            right: 0;
            cursor: ne-resize;
            transform: rotate(270deg);
        }

        .react-resizable-handle-w,
        .react-resizable-handle-e {
            top: 50%;
            margin-top: -10px;
            cursor: ew-resize;
        }

        .react-resizable-handle-w {
            left: 0;
            transform: rotate(135deg);
        }

        .react-resizable-handle-e {
            right: 0;
            transform: rotate(315deg);
        }

        .react-resizable-handle-n,
        .react-resizable-handle-s {
            left: 50%;
            margin-left: -10px;
            cursor: ns-resize;
        }

        .react-resizable-handle-n {
            top: 0;
            transform: rotate(225deg);
        }

        .react-resizable-handle-s {
            bottom: 0;
            transform: rotate(45deg);
        }
    }

    >.editor-resizer-item {
        display: flex;
        flex-direction: row;
        width: 100%;
        height: 100%;
        overflow: hidden;

        &-content {
            // display: flex;
            // width: calc(100% - 1px);
        }
    }
}

.vwood-resizer-vertical {
    flex-direction: column;


    >.editor-resizer-item {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 100%;
        overflow: hidden;

        &-content {
            // display: flex;
            // width: calc(100% - 1px);
        }
    }

    :global {
        .react-resizable-handle-vertical {
            display: block;
            width: 100%;
            height: 1px;
            background-repeat: no-repeat;
            background-origin: content-box;
            box-sizing: border-box;
            background-position: bottom right;
            cursor: row-resize;
            position: relative;
            background-color: rgb(221, 221, 221);

            &::after {
                content: "";
                display: inline-block;
                width: 100%;
                height: 12px;
                background-color: rgb(117, 117, 117);
                position: absolute;
                z-index: 10;
                transform: translateY(-50%);
            }
        }
    }
}